
import EncounterMixinVue from '@/views/EncounterMixin.vue'
import { FieldType } from "@/components/Forms/BaseFormElements"
import { defineComponent } from 'vue'
import { Field, FooterBtnEvent, Option } from '@/components/Forms/FieldInterface'
import { IonPage } from "@ionic/vue"
import { ObsValue } from '@/services/observation_service'
import { HtsReferralService } from "@/apps/HTS/services/hts_referral_service"
import { getFacilities } from '@/utils/HisFormHelpers/LocationFieldOptions'
import dayjs from 'dayjs'
import { toDate } from '@/utils/Strs'

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as any,
    isHivPositive: false as boolean
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsReferralService(this.patientID, this.providerID)
          this.fields.push(this.referralServicesField())
          this.fields.push(this.artAppointmentDataField())
          this.fields.push(this.intendedArtSiteField())
          this.fields.push(this.commentsField())
        }
      },
      immediate: true
    }
  },
  methods: {
    async onFinish(_: any, computedData: any) {
      const obs = await this.resolveObs(computedData)
      await this.service.createEncounter()
      await this.service.saveObservationList(obs as ObsValue[])
      this.nextTask()
    },
    getObs(concept: string) {
      return HtsReferralService.getFirstValueCoded(this.patientID, concept) 
    },
    serviceReferral(formData: any, service: string) {
      return formData.services.some((v: Option) => v.value === service)
    },
    referralServicesField(): Field {
      return {
        id: "services",
        helpText: 'Refer Client to other Services?',
        type: FieldType.TT_MULTIPLE_SELECT,
        isRequired: () => true,
        init: async () => {
          this.isHivPositive = (await this.getObs('HIV status')) === 'Positive'
          return true
        },
        finalComputedValue: (v: Option[]) => {
          return {
            obs: v.map(d => this.service.buildValueText('Referrals ordered', d.value)) 
          }
        },
        onValueUpdate(listData: Option[], value: Option) {
          return listData.map(l => {
            if (value.value === 'None' && l.value !='None') {
              l.isChecked = false
            }
            if (value.value != 'None' && value.isChecked && l.value === 'None') {
              l.isChecked = false
            }
            return l
          })
        },
        options: (f: any) => {
          const opt = (v: string) => {
            const selected = f.services ? f.services.some((d: Option) => d.value === v) : false
            return { label: v, value: v, isChecked: selected }
          }
          const options: any = []
          if (!this.isHivPositive) {
            options.push(opt('ART'))
          } else {
            options.push({ 
              label: 'ART',
              value: 'ART',
              isChecked: true,
              description: {
                color: 'primary',
                text: 'Client is HIV Positive. Leave this option selected'
              } 
            })
          }
          options.push(opt('STI'))
          options.push(opt('TB'))
          options.push(opt('Other'))
          if (this.patient.isMale()) options.push(opt('VMMC'))
          options.push(opt('Prep'))
          options.push(opt('Pep'))
          options.push(opt('None'))
          return options
        },
        config: {
          footerBtns: [
            {
              name: "None",
              slot: "end",
              onClickComponentEvents: {
                refreshOptions: (_: any, listData: Option[]) => {
                  return listData.map(o => {
                    o.isChecked = o.label === 'None'
                    return o
                  })
                }
              },
              onClick: () => 'None'
            }
          ]
        }
      }
    },
    intendedArtSiteField(): Field {
      return {
        id: 'art_site',
        helpText: 'Intended Site for ART',
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        condition: (f: any) => this.serviceReferral(f, 'ART'),
        finalComputedValue: ({ label }: Option) => ({
          obs: this.service.buildValueText('ART referral', label)
        }),
        options: (_: any, filter='') => getFacilities(filter),
        config: {
          footerBtns: [
            {
              name: "Here",
              slot: "end",
              onClickComponentEvents: {
                setValue: (btnEvent: FooterBtnEvent) => {
                  return btnEvent.btnOutput
                }
              },
              onClick: () => HtsReferralService.getLocationName()
            }
          ],
          showKeyboard: true,
          isFilterDataViaApi: true
        }
      }
    },
    artAppointmentDataField(): Field {
      return {
        id: "art_appointment_date",
        helpText: "Appointment Date for ART Referral",
        type: FieldType.TT_DATE_PICKER,
        isRequired: () => true,
        condition: (f: any) => this.serviceReferral(f, 'ART'),
        defaultValue: () => dayjs(this.service.date).add(1, 'day'),
        finalComputedValue: (d: Option) => {
          return {
            obs: this.service.buildValueDate('Antiretroviral patient appointment', d.value)
          }
        },
        config: {
          minDate: () => this.service.date,
          infoItems: (date: string) => {
            return [
              {
                label: 'Set appointment date',
                value: toDate(date)
              }
            ]
          },
          hiddenFooterBtns: [
            'Clear'
          ]
        }
      }
    },
    commentsField(): Field {
      return {
        id: 'comment',
        helpText: 'Comments (Optional)',
        type: FieldType.TT_NOTE,
        finalComputedValue: (v: Option | null) => {
          if (v && !/^\s*$/gi.test(`${v.value}`)) {
            return {
              obs: this.service.buildValueText('Comments', v.value)
            }
          }
          return null
        }
      }
    }
  }
})
